.newTodo{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    line-height: 18px;
    align-items: center;
    justify-content: center;
    background: url('./icon-add.png');
    background-size: 65%;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    
}

.newTodo p{
    font-size: 32px;
}



