header {
    padding: 10px 10px;
}


.logo-principal{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 0;

}


.logo-principal img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    animation: gira 1s reverse;
}

h1{
    font-size: 32px;
    margin-left: 10px;
    color: #0f0f0f;
}

h2 {
    text-align: left;
    background-color: #1fa525;
    text-transform: uppercase;
    padding: 10px 10px;
    color: #fff;
    font-size: 16px;
    margin-top: 0;
    margin-bottom: 0;
}

h2 span{
    font-size: 16px;
    font-weight: bolder;
    background: #fff;
    padding: 2px 10px;
    border-radius: 10px;
    color: #019108;

}


@keyframes gira {
    100%{
        transform: rotate(180deg);
    }
}

@media (max-width:500px){
    h2{
        font-size: 12px;
    }
}