.LoadingTodo-container{
    width: 100%;
    height: 15%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #6694b9;
    color: #e9e9e9;
    margin: 5px auto;
    padding: 2px 5px;
    box-shadow: 1px 1px 3px rgb(87, 86, 86);
    cursor: pointer;
}

.LoadingTodo-container{
    background: linear-gradient(90deg, #f0f8fcda, #1690f5b9);
    background-size: 400% 400%;
    box-shadow: none;
    animation: loadingAnimation 3s ease-in-out infinite;
}

@keyframes  loadingAnimation{
    0%{
        background-position: 0% 50;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}