h3{
    margin: 0;
    color: #7c7878;
    font-weight: bold;
}

.todoListNotification{
    display: none;
    padding: 0;
    margin: 0;
    color: rgb(98, 133, 139);
}
.todoListNotification--active{
    position: absolute;
    margin-left: 88px;
    margin-top: 100px;
    display: block;
    z-index: 4;
}

.todoListNotification h4{
    font-size: 25px;
    margin: 0;
    padding: 0;
}

.todoListNotification span{
    display: block;
    font-size: 60px;
}

ul {
    width: 95%;
    height: 50vh;
    margin: 0px auto;
    padding: 5px 10px;
    overflow-y: auto;
    overflow-x: hidden;
}

