button {
    position:relative;
    cursor: pointer;
    font-size: 40px;
    margin-top: 10px;
    padding: 5px 16px;
    border: none;
    background-color: #eb5146;
    color: #fff;
    border-radius: 50%;
    box-shadow: 1px 1px 10px #f16d14;

    z-index: 2;
}
button:hover{
    background-color: #eb6746;
    animation: gira .1s linear forwards;
}

@keyframes gira {
    100%{
        transform: rotate(45deg);
    }
}
