form {
    width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #f0f8fc;
    padding: 20px 30px 30px 20px;
    color: #6a7e94;
    box-shadow: 1px 1px 15px #7392ca;
}

form label {
    line-height: 2;
    text-align: center;
    font-size: 24px;
}

textarea {
    width: 100%;
    height: 150px;
    font-size: 14px;
    border-radius: 10px;
    padding: 10px 5px;
    resize: none;
}

.TodoForm-buttonContainer {
    margin-top: 15px;
    display: flex;
    justify-content: space-around;
}

.TodoForm-button {
    width: 100px;
    font-size: 16px;
    border-radius: 10px;
}

.TodoForm-button:hover {
    animation: none;
}

.Todo-form--add {
    background-color: #2298f8;
    box-shadow: 1px 1px 3px rgb(87, 86, 86);
}

.Todo-form--add:hover{
    background-color: green;
}

.Todo-form--cancel {
    background-color: #838383;
    box-shadow: 1px 1px 3px rgb(87, 86, 86);
}