span {
    position: relative;
    font-size: 22px;
    margin: 0 5px;
}

li {
    width: 100%;
    height: 15%;
    display: flex;
    font-size: 14px;
    justify-content: space-between;
    align-items: center;
    background-color: #238fe7;
    color: #e9e9e9;
    margin: 5px auto;
    padding: 2px 5px;
    box-shadow: 1px 1px 3px rgb(87, 86, 86);
    cursor: pointer;

}

li:hover {
    color: #238fe7;
    background-color: #f0f8fc;
}

.bi-check-circle-fill:hover {
    color: green;
}

.bi-x-circle-fill:hover {
    color: red;
}

.bi-x-circle-fill:hover~.tooltipItem {
    visibility: visible;
}

.bi-check-circle-fill:hover~.tooltipItem {
    visibility: visible;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

.icon-check--active{
    background-color: #f0f8fc;
    color: rgb(138, 138, 138);
}

.tooltipItem {
    position: absolute;
    background-color: rgb(227, 227, 230);
    color: #333333;
    width: 68px;
    padding: 2px 1px;
    left: -30px;
    top: 30px;
    font-size: 9px;
    border-radius: 5px;
    visibility: hidden;
}

.tooltipItem:hover{
    visibility: visible;
}