input {
    width: 92%;
    margin: 5px 0px 20px 0px;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 10px;
    background-color: #fff;
    color: rgb(5, 5, 5);
    border: none;
}

input:focus {
    outline: none;
}

input::placeholder{
    color: rgba(95, 89, 95, 0.466);
}